.stats-page .card {
  margin-bottom: 1.5rem;
}
.stats-page .stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid var(--surface-border);
}
.stats-page .stat-item:last-child {
  border-bottom: none;
}
.stats-page .p-progressbar {
  height: 2rem;
}
.stats-page .p-chart {
  height: 100%;
  min-height: 300px;
}
.stats-page .stat-box {
  padding: 1rem;
}
